<template>
  <div class="importexcel">
    <el-card>
      <div slot="header">
        <span class="title">Excel导入</span>
      </div>
      <el-form label-width="100px">
        <el-form-item label="数据来源">
          <strong class="box-inp-file">
            <input
              ref="btn-inp-file"
              v-on:change="selectFile"
              class="inp-file"
              type="file"
              accept="application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            >
          </strong>
        </el-form-item>
        <el-form-item label="样本名称">
          <el-input v-model.trim="name"></el-input>
        </el-form-item>
        <el-form-item label="样本范围说明">
          <el-input v-model.trim="description"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="upLoadFile">确定</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import {importSample} from '@/api/sample';
export default {
  name: 'page-importexcel',
  data() {
    return {
      file: null,
      name: '',
      description: '',
      cansubmit: true
    };
  },
  methods: {
    selectFile() {
      //console.log("======执行selectFile方法=====");
      this.file = this.$refs['btn-inp-file'].files[0];
      console.log(this.file);
    },
    check() {
      if (!this.file) {
        this.$message.warning('请选择文件');
        return;
      }
      if (this.name.trim().length === 0) {
        this.$message.warning('请填写样本名称');
        return;
      }
      if (this.description.trim().length === 0) {
        this.$message.warning('请填写样本范围说明');
        return;
      }
      return true;
    },
    upLoadFile() {
      if (!this.cansubmit) {
        return;
      }
      this.cansubmit = false;
      let checked = this.check();
      if (!checked) {
        this.cansubmit = true;
        return;
      }
      importSample(this.name, this.description, this.file)
        .then(result => {
          this.$message.success('导入成功');
          setTimeout(function() {
            this.cansubmit = true;
          }, 1000);
        })
        .catch(err => {
          console.log(err);
          this.cansubmit = true;
        });
    }
  },
  mounted() {
    console.log('=========view mounted=========');
  }
};
</script>

<style scoped>
.el-card {
  margin: 20px 15px;
}
.el-input {
  width: 220px;
}
</style>
